.slick-arrow:after { display: block; content: ""; }

.slick-slider { position: relative; display: block; box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; perspective: 800px; -webkit-perspective: 800px; -moz-perspective: 800px; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; border: 1px solid transparent; }

.slick-arrow.slick-hidden { display: none; }

.slick-dots { z-index: 5; position: absolute; left: 0; bottom: -25px; width: 100%; }

.slick-dots li { list-style: none; display: inline-block; margin-right: 12px; }

.slick-dots button { font-size: 0; width: 8px; height: 8px; border-radius: 8px; border: 1px solid #156692; transition: .5s; }

.slick-dots button:hover { background: #ff0000; }

.slick-dots .slick-active button { background: #156692; }

.slick-arrow { position: absolute; top: 50%; z-index: 3; width: 42px; height: 42px; margin-top: -21px; font-size: 0; border: none; background: none; transition: .5s; }

.slick-arrow:after { width: 20px; height: 20px; border-top: 1px solid #ffffff; border-left: 1px solid #ffffff; position: absolute; top: 10px; }

@media (min-width: 1201px) { .slick-arrow:hover { opacity: .7; } }

.slick-arrow.slick-disabled { opacity: .2; }

.slick-prev { left: 5px; }

.slick-prev:after { left: 17px; transform: rotate(-45deg); }

.slick-next { right: 5px; }

.slick-next:after { right: 17px; transform: rotate(135deg); }

/* slider */
.slider__item { height: 100%; background-position: 50% 50%; background-size: cover; display: flex !important; align-items: center; padding: 45px 55px 0; }

.slider__item_i1 { background-image: url("../images/b1.jpg"); }

.slider__item_i2 { background-image: url("../images/b2.jpg"); }

.slider__item_i3 { background-image: url("../images/b3.jpg"); }

.slider__item_i4 { background-image: url("../images/b4.jpg"); }

.slider__item_i5 { background-image: url("../images/b5.jpg"); }

.slider__discount { font-size: 75px; font-weight: 700; color: #ffffff; }

.slider__logo { width: 120px; }

.slider__logo img { max-width: 100%; height: auto; }

.slider__title { font-size: 38px; font-weight: 700; line-height: 1.2; letter-spacing: 0.02em; color: #ffffff; max-width: 650px; margin-top: 25px; }

.slider__desc { line-height: 1.5; color: #ffffff; max-width: 500px; margin-top: 15px; }

.slider, .slider .slick-list, .slider .slick-track { height: 100%; }

@media (max-width: 750px) { .slider__discount { font-size: 50px; }
  .slider__logo { width: 100px; }
  .slider__item { padding: 0; }
  .slider__item_i1 { background-image: url("../images/b1-mob.jpg"); }
  .slider__item_i2 { background-image: url("../images/b2-mob.jpg"); }
  .slider__item_i3 { background-image: url("../images/b3-mob.jpg"); }
  .slider__item_i4 { background-image: url("../images/b4-mob.jpg"); }
  .slider__item_i5 { background-image: url("../images/b5-mob.jpg"); }
  .slider__title { font-size: 24px; }
  .slider .slick-arrow { top: auto; bottom: 10px; } }
